}

p {
  font-family: 'Raleway', sans-serif !important;
}

.heading {
  text-align: center;
}

.heading span {
  position: absolute;
  font-size: 150px;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  z-index: -1;
  color: #c1fff04a;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
  background-color: #0ed9a8;
}

span[aria-label="Loading Spinner"] {
  position: fixed !important;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
}

.btn-G {
  background-color: #0ed9a8;
  padding: 12px 28px;
  color: #fff !important;
  -webkit-text-decoration: none;
  text-decoration: none !important;
  font-size: 17px !important;
  box-shadow: 0px 0px 10px #dfdfdf;
  display: inline-block;
  border: 1px solid transparent;
  transition: .4s;
  font-family: 'Cinzel', serif !important;
  cursor: pointer;

}
.btn-G:hover {
  background: transparent !important;
  border-color: #0ed9a8 !important;
  color: #0ed9a8 !important;
  box-shadow: 0px 0px 5px 0px #81f4d9 !important;
}
@-webkit-keyframes grow {
  0%, 40%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes grow {
  0%, 40%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.whatofferlist li {
  font-family: 'Raleway',sans-serif;
  font-size: 20px;
  margin-bottom: 30px;
  position: relative;
}
.whatofferlist li::after {
  position: absolute;
  content: '';
  width: 8px;
  height: 8px;
  background: #0ed9a8;
  left: -25px;
  top: 10px;
  border-radius: 50%;
  -webkit-animation: 2s grow ease-in-out infinite;
  -webkit-animation: 2s grow ease-in-out infinite;
  animation: 2s grow ease-in-out infinite;
}
input:focus-visible,
textarea:focus-visible
{
  outline: none;
  border: none;
}
